.NoteForm form {
  margin: 0.5em 0 0.5em 0;
  border: 1px solid #676767;
  display: -ms-flexbox;
  display: flex;
  width: 90%;
  margin-right: auto;
  margin-left: auto;
  -ms-flex-direction: column;
      flex-direction: column;
}

.NoteForm form textarea {
  font-size: 1em;
  padding: 0.5em;
  font-family: 'Maitree', serif;
  resize: none;
}

.NoteForm input[type="title"] {
  font-family: 'Maitree', serif;
  font-size: 1em;
  padding: 0.5em;
  border-bottom: 1px solid #565656;
}

.NoteForm input[type="submit"] {
  font-size: 1em;
  padding: 0.2em 0.5em 0.2em 0.5em;
  border-top: 1px solid #565656;
  background-color: #efefef;
}

.NoteForm input[type="submit"]:hover {
  cursor: pointer;
  background-color: #7AA87A;
}

/* Ipads ----------- */
@media only screen
and (min-width : 600px) {
  .NoteForm form {
    width: 75%;
  }
}

/* Desktops and laptops ----------- */
@media only screen
and (min-width : 1000px) {
  .NoteForm form {
    width: 75%;
  }
}



